.social {
  display: flex;
  align-items: center;

  margin-top: 30px;
  margin-left: 10px;

  @include vp-1023 {
    margin-top: 0;
  }
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 40px;
  height: 40px;
  margin-right: 15px;

  &:last-child {
    margin-right: 0;
  }
}